@import '../../css/variables.css';

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.header {
  flex: none;
  padding: 20px 10px;
  letter-spacing: 0.1em;
  background-color: rgb(25, 32, 35);
  background-image: linear-gradient(to right, rgb(26, 43, 62), rgb(20, 30, 48));
}
.header_menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header_menu_item {
  font-family: 'Arvo', serif;
  font-size: 18px;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: var(--primary-color);
  text-decoration: none;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 10px;
  background-color: rgb(25, 32, 35);
  background-image: linear-gradient(to right, rgb(26, 43, 62), rgb(20, 30, 48));
}

.icon_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon_text {
  color: var(--primary-color);
  font-size: 18px;
}

.icon_link {
  text-decoration: none;
}

.icon_link:hover .icon_github {
  fill: var(--primary-color);
}

.icon_github {
  margin-left: 8px;
  width: 36px;
  height: 36px;
  fill: black;
}
