.search_hero_wrapper {
  position: relative;
  display: block;
  width: 240px;
  margin-left: auto;
  margin-right: auto;
}

.hero_search_input {
  display: block;
  width: 240px;
  height: 35px;
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 10px;

  border-radius: 30px;
  font-family: 'Alegreya Sans', sans-serif;
  font-size: 18px;
  outline: none;
  caret-color: var(--primary-color);
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.05);
  border-color: rgba(18, 44, 56, 0.7);
}

.hero_search_input::placeholder {
  font-size: 18px;
  text-align: center;
  color: rgb(20, 16, 16);
}

/* .hero_search_input:hover {
  background-color: red;
} */
.search_icon {
  position: absolute;
  top: 8px;
  left: 10px;
  transition: all 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
}
.hero_search_input:focus + .search_icon {
  fill: red;
}

.text {
  position: absolute;
  top: 8px;
  left: 70px;

  font-size: 18px;
  color: rgb(20, 16, 16);
  pointer-events: none;
  transition: transform 600ms cubic-bezier(0.075, 0.82, 0.165, 1),
    font-size 250ms cubic-bezier(0.075, 0.82, 0.165, 1),
    color 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.hero_search_input:focus ~ .text {
  color: red;
  transform: translate(-55px, -25px);
  font-size: 0.85rem;
  color: #8eb9da;
}
