.container {
  padding-left: 15px;
  padding-right: 15px;
  @media screen and (min-width: 480px) {
    margin-left: auto;
    margin-right: auto;
    width: 480px;
  }

  @media screen and (min-width: 768px) {
    width: 760px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}

.main {
  flex: 1 1 auto;
  background: linear-gradient(
    90deg,
    rgba(12, 18, 23, 1) 0%,
    rgba(144, 144, 144, 1) 100%
  );
}

.card_wrapper {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  color: var(--primary-color);
}

.card {
  position: relative;
  max-width: 264px;
  padding: 8px 0;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 4px;
  background-color: rgb(14, 30, 48);
  border-radius: 10px;
  transition: all 250ms ease;
}
.card:hover {
  box-shadow: 0px 4px 10px rgb(14, 30, 48);
  transform: scale(1.05);
}

.card_hero_name {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: 500;
}

.card_characteristics_wrapper {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.card_characteristics::before {
  content: '';
  display: inline-block;
  margin-right: 6px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
}

.card_characteristics_red::before {
  background-color: red;
}

.card_characteristics_green::before {
  background-color: green;
}

.card_characteristics_blue::before {
  background-color: blue;
}

.card_hero_role {
  width: 100%;
  padding: 4px 5px 0px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 4px;
}

.card_star {
  position: absolute;
  top: 175px;
  right: 8px;
  display: block;
  width: 30px;
  height: 30px;
}

.card_star:hover {
  cursor: pointer;
  fill: #ffd700;
  stroke: #ffd700;
}
